.plug {
  position: relative;
  z-index: 5;
  &__container {
    display: flex;
    justify-content: space-between;
    padding: 2.7rem 3.7rem;
    background-color: #fff;
    box-shadow: 0 4px 20px 0 rgba(199, 219, 239, 0.5);
    border-radius: 1rem;
    position: relative;

    &::before {
      content: "";
      position: absolute;
      z-index: -1;
      width: 95%;
      height: 100%;
      background-color: #fff;
      box-shadow: 0 4px 20px 0 rgba(199, 219, 239, 0.5);
      border-radius: 1rem;
    }
  }
  &__pl {
    position: relative;

    &::before {
      content: "";
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      width: 1px;
      height: 100%;
      background: #e0e0e0;
    }
  }

  &__col {
    flex: 1;
    flex-shrink: 0;
    flex-basis: 30rem;

    display: flex;
    flex-direction: column;
    justify-content: space-between;

    .hero-top__text {
      font-size: 1.6rem;
    }

    &:not(:first-child) {
      padding-left: 6rem;
      padding-top: 0.9rem;
    }
    &:not(:last-child) {
      padding-right: 6rem;
    }
  }
  &__author {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 3rem;
  }
  &__name {
    display: block;
    margin-bottom: 1.2rem;
  }
  &__image {
    width: 11.5rem;
    height: 11.5rem;
    border-radius: 100%;
    flex-shrink: 0;
    img {
      width: 100%;
      height: 100%;
      border-radius: 100%;
    }
  }
}
